import Application from "../application";
import StatsView from "./stats-view";
import {changeView} from "../utils";

class StatsScreen {
  init(state) {
    this.view = new StatsView(state);
    changeView(this.view);
    this.view.onBack = () => {
      Application.showGreeting();
    };
  }
}

export default new StatsScreen();


